$메인칼라변수 : #ff0000;
@mixin name {
    background:#eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
    p{
        margin-bottom: 0;
    }
}

.red{
    color: $메인칼라변수;

}
.my-alarm{
   @include name()
}
.my-alarm2{
    @extend.my-alarm;
    background: #ffee59;
}



